"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { toast } from "sonner";

import { authClient } from "@/lib/auth-client";
import Loader from "./loader";

const GoogleIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" aria-hidden className="size-5">
    <path
      d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
      fill="#4285F4"
    />
    <path
      d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
      fill="#34A853"
    />
    <path
      d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l3.66-2.84z"
      fill="#FBBC05"
    />
    <path
      d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
      fill="#EA4335"
    />
  </svg>
);

export default function SignInGoogle() {
  const router = useRouter();
  const { data: session, isPending } = authClient.useSession();

  useEffect(() => {
    if (session?.user) {
      router.replace("/cms/projects");
    }
  }, [session, router]);

  if (isPending || session?.user) {
    return (
      <div className="flex min-h-dvh items-center justify-center bg-[var(--p-cream)]">
        <Loader />
      </div>
    );
  }

  const handleSignIn = () => {
    const callbackURL =
      typeof window !== "undefined"
        ? `${window.location.origin}/cms/projects`
        : "/cms/projects";

    authClient.signIn.social(
      { provider: "google", callbackURL },
      {
        onError: (error) => {
          toast.error(
            (error as { error?: { message?: string; statusText?: string } })
              .error?.message ??
              (error as { error?: { statusText?: string } }).error?.statusText ??
              "Sign in failed",
          );
        },
      },
    );
  };

  return (
    <div className="flex min-h-dvh">
      {/* Left panel — brand */}
      <div className="hidden flex-1 flex-col justify-between bg-[var(--p-deep-walnut)] p-12 lg:flex">
        <div>
          <span className="text-xs font-semibold uppercase tracking-widest text-[var(--p-tan)]">
            Content Studio
          </span>
        </div>
        <div>
          <p className="text-3xl font-bold leading-snug text-balance text-[var(--p-white)]">
            &ldquo;Craftsmanship is not a shortcut. It is the long way done
            right.&rdquo;
          </p>
          <div className="mt-6 h-px w-12 bg-[var(--p-tan)]" aria-hidden />
          <p className="mt-4 text-xs font-medium uppercase tracking-widest text-[var(--p-tan)]">
            Custom Cabinetry
          </p>
        </div>
        <div>
          <p className="text-xs text-[var(--p-white)]/40">
            Authorised access only
          </p>
        </div>
      </div>

      {/* Right panel — sign in */}
      <div className="flex flex-1 flex-col items-center justify-center bg-[var(--p-cream)] px-6 py-12">
        <div className="w-full max-w-sm">
          {/* Mobile-only label */}
          <span className="mb-6 block text-xs font-semibold uppercase tracking-widest text-[var(--p-toffee)] lg:hidden">
            Content Studio
          </span>

          <h1 className="text-2xl font-bold text-balance text-[var(--p-deep-walnut)]">
            Sign in
          </h1>
          <div className="mt-3 h-px w-10 bg-[var(--p-tan)]" aria-hidden />
          <p className="mt-4 text-sm text-pretty text-[var(--p-coffee-bean)]">
            Use your Google account to access the content management area.
          </p>

          <div className="mt-8">
            <button
              type="button"
              onClick={handleSignIn}
              className="flex w-full items-center justify-center gap-3 border border-[var(--p-tan)] bg-[var(--p-white)] px-5 py-3 text-sm font-semibold text-[var(--p-deep-walnut)] outline-none transition-shadow hover:shadow-sm focus-visible:ring-2 focus-visible:ring-[var(--p-deep-walnut)] focus-visible:ring-offset-2"
            >
              <GoogleIcon />
              Continue with Google
            </button>
          </div>

          <p className="mt-8 text-center text-xs text-[var(--p-toffee)]/70">
            Only authorised administrators can access this area.
          </p>
        </div>
      </div>
    </div>
  );
}
